'use client';

export default function Timeline() {
  const timeline = [
    {
      phase: 'Phase 1',
      title: 'UI/UX Design',
      description: 'Create intuitive interfaces and user experiences',
      status: 'In Progress'
    },
    {
      phase: 'Phase 2',
      title: 'MVP Build',
      description: 'Core features and vehicle tracking system',
      status: 'Upcoming'
    },
    {
      phase: 'Phase 3',
      title: 'AI Integration',
      description: 'Machine learning models and diagnostic engine',
      status: 'Upcoming'
    },
    {
      phase: 'Phase 4',
      title: 'Partner Onboarding',
      description: 'Connect repair shops and insurance providers',
      status: 'Upcoming'
    },
    {
      phase: 'Phase 5',
      title: 'Beta Launch',
      description: 'California market test and user feedback',
      status: 'Upcoming'
    }
  ];

  return (
    <section id="timeline" className="relative z-10 max-w-7xl mx-auto px-6 py-20">
      <div className="text-center mb-16">
        <h2 className="text-5xl md:text-6xl font-bold mb-6">
          Our <span className="gradient-text">Roadmap</span>
        </h2>
        <p className="text-xl text-gray-400">From concept to launch</p>
      </div>

      <div className="space-y-6">
        {timeline.map((item, index) => (
          <div
            key={index}
            className="glass-strong rounded-2xl p-8 hover:scale-105 transition-transform"
          >
            <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
              <div className="flex items-center space-x-6">
                <div className="text-4xl font-bold text-[#2ec8c6]">{item.phase}</div>
                <div>
                  <h3 className="text-2xl font-bold mb-2">{item.title}</h3>
                  <p className="text-gray-400">{item.description}</p>
                </div>
              </div>
              <div className={`px-6 py-2 rounded-full font-semibold ${
                item.status === 'In Progress' 
                  ? 'bg-[#2ec8c6] text-black' 
                  : 'glass text-gray-400'
              }`}>
                {item.status}
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}

